
.article {
  padding-top: 20px;
  margin-bottom: 50px;
}
.article .headline {
  border-bottom: 4px solid #f6f6f6;
}
.article .headline .headline-background-link {
  line-height: 2em;
  position: relative;
  display: block;
  padding: 20px 45px 20px 20px !important;
  text-decoration: none !important;
}
.article .headline .headline-background-link .heading {
  color: #999;
  font-size: 15px !important;
  margin-bottom: 8px;
  line-height: 1em;
}
.article .headline .headline-background-link .heading-content {
  color: #444;
  font-size: 17px !important;
  line-height: 1.2em;
}
.article .headline .icon-arrow-right {
  position: absolute;
  top: 50%;
  right: 20px;
  background-image: url("http://static.daily.zhihu.com/img/share-icons.png");
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: middle;
  background-position: -70px -20px;
  width: 10px;
  height: 15px;
  margin-top: -7.5px;
}
.article h1 {
  position: absolute;
  bottom: 10px;
  padding: 0 40px;
  color: #fff;
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
  z-index: 1;
}
.article .img-wrap {
  position: relative;
  width: 100%;
  height: 400px;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
}
.article .img-wrap span {
  position: absolute;
  bottom: 10px;
  z-index: 1;
  font-size: 12px;
  color: rgba(255,255,255,0.6);
  right: 10px;
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
}
.article .img-wrap .img-mask {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(270deg, rgba(0,0,0,0) 65%, rgba(0,0,0,0.6));
  background: -moz-linear-gradient(270deg, rgba(0,0,0,0) 65%, rgba(0,0,0,0.6));
  background: -o-linear-gradient(270deg, rgba(0,0,0,0) 65%, rgba(0,0,0,0.6));
  background: -ms-linear-gradient(270deg, rgba(0,0,0,0) 65%, rgba(0,0,0,0.6));
  background: linear-gradient(180deg, rgba(0,0,0,0) 65%, rgba(0,0,0,0.6));
}
.article article {
  font-size: 15px;
}
.article article ol,
.article article ul {
  padding-left: 20px;
}
.article article .main-wrap {
  -webkit-font-smoothing: antialiased;
}
.article article .question-title {
  line-height: 1.4em;
  color: #494b4d;
  font-weight: 700;
  font-size: 18px;
  margin: 20px 0;
}
.article article .meta {
  white-space: nowrap;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 16px;
  color: #b8b8b8;
}
.article article .meta span {
  vertical-align: middle;
}
.article article .meta .author {
  color: #444;
  font-weight: 700;
}
.article article .meta .bio {
  color: #999;
}
.article article .comment-meta .avatar,
.article article .meta .avatar {
  width: 20px;
  height: 20px;
  border-radius: 2px;
  margin-right: 5px;
  vertical-align: middle;
}
.article article .content {
  font-size: 17px;
  color: #616466;
}
.article article .content img {
  width: 100%;
}


